<template>
  <div class="goods">
    <div class="goods_menu">
      <ul>
        <li v-for="item in goodsList" class="menu_item">
          <span>{{ item.name }}</span>
        </li>
      </ul>
    </div>
    <div class="goods_cont">
      <div v-for="item in goodsList">
        <h3 class="goods_title">{{ item.name }}</h3>
        <ul>
          <li v-for="foods in item.foods" class="food_item" :style="{borderColor:$index !== item.foods.length-1 ? 'rgba(7,17,27,.1)' : 'transparent'}">
            <div class="goods_icon">
              <img  :src=" foods.icon ">
            </div>
            <div class="goods_details">
              <div class="goods_details_title">{{ foods.name }}</div>
              <div class="goods_details_name" v-if="foods.description">{{ foods.description }}</div>
              <div>
                <span class="goods_details_sale">月售{{ foods.sellCount }}份</span>
                <span class="goods_details_praise">好评率{{ foods.rating }}%</span>
              </div>
              <div class="details_price">
                <span class="goods_details_price"><sub>￥</sub>{{ foods.price }}</span>
                <span class="goods_details_oldprice" v-if="foods.oldPrice">{{ foods.oldPrice }}</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      goodsList: {}
    };
  },
  mounted () {
    let _this = this;
    this.axios.get('./api/goods').then(function (res) {
      _this.goodsList = res.data.data;
      console.log(_this.goodsList);
    });
  }
};
</script>

<style>
  .goods{
    display: flex;
    position: absolute;
    top: 179px;
    bottom: 46px;
    width: 100%;
    overflow: hidden;
  }
  /* 左侧导航 */
  .goods_menu{
    flex: 0 0 80px;
    width: 80px; /* 兼容 */
    background-color: #f3f5f7;
  }
  .menu_item{
    display: table;
    padding: 0 12px;
    height: 48px;
    width: 56px; 
  }
  .menu_item span{
    display: table-cell;
    height: 48px;
    vertical-align: middle;
    line-height: 14px;
    font-size: 12px;
    border-bottom: 1px solid rgba(7,17,27,.1);
  }
  /* 右侧内容 */
  .goods_cont{
    flex: 1;
  }
  .goods_title{
    background-color: #f3f5f7;
    color: rgb(147,153,159);
    border-left: 2px solid #d9dde1;
    padding-left: 14px;
    font-size: 15px;
    line-height: 26px;
    height: 26px;
  }
  .food_item{
    display: flex;
    margin: 18px;
    border-bottom: 1px solid rgba(7,17,27,.1);
  }
  .goods_details{
    padding: 0 18px 18px 10px;
  }
  .goods_icon{
    width: 72px;
    height: 72px;
  }
  .goods_icon>img{
    width: 100%;
    height: 100%;
  }
  .goods_details_title{
    font-size: 16px;
    color: rgb(7,17,27);
    line-height: 18px;
    padding: 4px 0;
    font-weight: 600;
  }
  .goods_details_name,.goods_details_sale,.goods_details_praise{
    font-size: 12px;
    color: rgb(147,153,159);
    padding: 8px 0;
  }
  .goods_details_sale{
    padding-right: 12px;
  }

  .goods_details_price,.goods_details_oldprice{
    display: inline-block;
    font: 18px;
    color: rgb(240,20,20);
    padding: 8px 8px 0 0;
    font-weight: bold;
  }
  .goods_details_price>sub,.goods_details_oldprice>sub{
    font-size: 12px;
    font-weight: bold;
  }
</style>



